.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 50px;
}

.nav {
  &__list {
    display: flex;
    padding: 23px 0;
  }
  &__item {
    position: relative;
    list-style-type: none;
    text-transform: uppercase;

    &:not(:last-child) {
      margin-right: 20px;
    }
  }
  &__link {
    padding: 23px 0;
    font-weight: 500;
    color: #060b35;
    line-height: 14px;
    text-decoration: none;
    cursor: pointer;

    &:hover {
      color: $HTMLbutton-background;
    }

    &--is-active {
      color: $HTMLbutton-background;
    }

    &--is-active:after {
      position: absolute;
      content: "";
      height: 4px;
      background-color: $HTMLbutton-background;
      left: 0;
      right: 0;
      bottom: -23px;
      border-radius: 8px;
    }
  }
}
